export default {
	props: {
		iconClass: {
			type: String
		},
		listContent: {
			type: String,
			default() {
				return '列表'
			}
		},
		needArrow: {
			type: Boolean,
			default() {
				return false
			}
		}
	},
	data() {
		return {
			arrow: this.needArrow
		}
	},
	methods: {
		listClick() {
			this.$emit('onListClick', this.listContent)
		}
	}
}
